#mainstream-div h2 {
  font-size: 48px;
  color: black;
  /* text-shadow: 0 0 10px blue; */
  margin-bottom: .25em;
  text-align: center;
}

.mainstream-paper {
  background-size: 24em;
  background-repeat: no-repeat;
  background-position: 50% 10%;
  margin-top: 1em;
  margin-left: 1em;
  margin-right: 1em;
  /* margin: 2em; */
  margin-bottom: 0em;
  height: 43.25em;
  /* height: 36em; */
  /* filter: drop-shadow(0 0 0.75rem orange); */
}

#mainstream-patreon-link {
  /* text-decoration: none; */
  cursor: url('../../assets/cursor/sym57.cur'), auto;
  color:black;
}

#mainstream-patreon-link:visited {
  color: black;
}

.mainstream-marquee-text {
  font-size: 16px;
  font-weight: bold;
  margin-top: .5em;
  margin-bottom: .5em;
  color: blue;
}


.announcement-grid {
  margin-top: 18em;
}

.announcement-plain-text{
  font-size: 28px;
  color: black;
  font-weight: bold;
}

.announcement-headline {
  font-size: 48px;
  color: black;
}

.announcement-sub-headline {
  font-size: 42px;
  color: black;
  text-decoration: none;
}

.announcement-patreon-link{
  cursor: url('../../assets/cursor/sym57.cur'), auto;
  text-decoration: none;
}

/* guitarists */
/* guitarist head */
.guitarist-skull{
  height: 3.25em;
  margin-top: -3em;
  margin-bottom: -7.55em;
  margin-left: 0.1em;
  z-index: 2;
  position: relative;
}

@media (prefers-reduced-motion: no-preference) {
  .guitarist-skull {
    animation: guitarist-head-nod infinite alternate .7s linear;
  }
}

@keyframes guitarist-head-nod {
  from {
    transform: rotate(-10deg);
  }
  to {
    transform: rotate(10deg);
  }
}

/* guitarist bodies */
.guitarist-torso{
  height: 2.75em;
  margin-top: -2em;
  margin-bottom: -8.6em;
  margin-left: 0em;
}

@media (prefers-reduced-motion: no-preference) {
  .guitarist-torso {
    animation: skeleton-guitarist-body-rock infinite alternate-reverse .70s linear;
  }
}

@keyframes skeleton-guitarist-body-rock {
  from {
    transform: rotate(-3deg);
  }
  to {
    transform: rotate(3deg);
  }
}

.guitarist-legs{
  height: 5.5em;
  margin-top: -8em;
  margin-bottom: -9.4em;
  margin-left: 0em;
}

@media (prefers-reduced-motion: no-preference) {
  .guitarist-legs {
    animation: guitarist-legs-rock infinite alternate-reverse .70s linear;
  }
}

@keyframes guitarist-legs-rock {
  from {
    transform: rotate(-2deg);
  }
  to {
    transform: rotate(2deg);
  }
}


/* guitarist right arm */
.guitarist-right-arm {
  height: 3.5em;
  margin-top: -3em;
  margin-bottom: -8.3em;
  margin-left: -3.55em;
  position: relative;
  z-index: 3;
}

@media (prefers-reduced-motion: no-preference) {
  .guitarist-right-arm {
    animation: guitar-right-arm-strum infinite alternate .70s linear;
  }
}

@keyframes guitar-right-arm-strum {
  from {
    transform: rotate(10deg);
    /* transform: rotate(-14deg); */
  }
  to {
    transform: rotate(-14deg);
    
  }
}

.guitarist-left-arm {
  height: 2.75em;
  margin-top: -2em;
  margin-bottom: -6.3em;
  margin-left: 6em;
  position: relative;
  z-index: 2;
}

@media (prefers-reduced-motion: no-preference) {
  .guitarist-left-arm {
    animation: guitar-left-arm-strum infinite  alternate .70s linear;
  }
}

@keyframes guitar-left-arm-strum {
  from {
    transform: rotate(-6deg);
  }
  to {
    transform: rotate(6deg);
  }
}

/* guitars */
.mainstream-guitar{
  height: 3.75em;
  margin-bottom: -7.25em;
  margin-top: 0em;
  margin-left: -.2em; 
  z-index: 1;
  position:relative;
}

@media (prefers-reduced-motion: no-preference) {
  .mainstream-guitar {
    animation: mainstream-guitar-spin infinite alternate .70s linear;
  }
}

@keyframes mainstream-guitar-spin {
  from {
    transform: rotate(-14deg);
  }
  to {
    transform: rotate(-4deg);
  }
}


/* drummer */
#mohawk-skull {
  height: 4.65em;
  margin-top: -4em;
  margin-bottom: -1.25em;
  margin-left: -0.1em;
  z-index: 3;
  position: relative;
}

@media (prefers-reduced-motion: no-preference) {
  #mohawk-skull {
    animation: mohawk-head-nod infinite alternate .70s linear;
  }
}

@keyframes mohawk-head-nod {
  from {
    transform: rotate(10deg);
  }
  to {
    transform: rotate(-10deg);
  }
}

.drummer-body {
  height: 5.25em;
  margin-top: -5.75em;
  margin-bottom: 1.25em;
  margin-left: -0.2em;
  z-index: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .drummer-body{
    animation: drummer-body-rock infinite alternate-reverse .70s linear;
  }
}

@keyframes drummer-body-rock {
  from {
    transform: rotate(8deg);
    /* transform: rotate(0deg); */
  }
  to {
    transform: rotate(-8deg);
    /* transform: rotate(0deg); */
  }
}

.drummer-right-arm {
  height: 4.35em;
  margin-top: -1.3em;
  margin-bottom: -.6em;
  margin-left: 0.2em;
  position: relative;
  z-index: 3;
}


@media (prefers-reduced-motion: no-preference) {
  .drummer-right-arm{
    animation: drummer-right-arm-rock infinite alternate-reverse .70s linear;
  }
}

@keyframes drummer-right-arm-rock {
  from {
    transform: rotate(-180deg);
    /* transform: rotate(-60deg); */
  }
  to {
    transform: rotate(-60deg);
  }
}

.drummer-left-arm {
  height: 3.45em;
  margin-top: -3em;
  margin-bottom: 2.4em;
  margin-left: .85em;
  position: relative;
  z-index: 2;
}

@media (prefers-reduced-motion: no-preference) {
  .drummer-left-arm{
    animation: drummer-left-arm-rock infinite alternate .70s linear;
  }
}

@keyframes drummer-left-arm-rock {
  from {
    transform: rotate(50deg);
  }
  to {
    transform: rotate(30deg);
  }
}


/* drums */
.drums {
  height: 8em;
  margin-top: -8em;
  margin-bottom: 0em;
  padding-bottom: 0em;
  position: relative;
  z-index: 1;
}

/* audio-player */
.mainstream-react-h5-audio-player {
  margin-top: 3.5em;
  margin-bottom: 0em;
  outline: black solid 2px;
  position: relative;
  /* position: absolute; */
  /* height: 6.5em; */
}

/* cursor */
.rhap_container svg  {
  cursor: url('../../assets/cursor/sym57.cur'), auto;
}


